import React from 'react';
import "./Teja.css";
import { FaQuoteLeft } from "react-icons/fa";
import { v1 as uuidv1 } from 'uuid';

function Teja() {
    var projects = ["Music App", "E-commerce Webapp", "Simple Intrest Calculator","Portfolio Websites","Next JS Portfolio","React Timer"]
    var skills=["HTML","CSS","JavaScript","React","Jquery","GraphQl","Node Js","Express Js","MY SQL"]
    var certifications=["React by Udemy","Intoduction to cloud computing using javascript by IBM","Complete modern javascript with firebase by Udemy", "Web Development Masterclass","React+Redux by Sololearn"]
    return (
        <div className="mainn">
            <div className="about">
                <h1 className="edu">ABOUT ME</h1>
                <p className="lefty">I am Teja, a web designer/developer focused on crafting great web experiences. Designing have been my passion since the days I started working with computers but I found myself into web design/development. I enjoy creating beautifully designed, intuitive and functional websites..</p>
            </div>
            <div className="education">
                <h1 className="edu">EDUCATION</h1>
                <div className="division">
                    <div className="Engineering">
                        <h1>Engineering</h1>
                        <p>
                            <li>2017 - 2021</li>
                            <li>Mechanical Department</li>
                            <li>RMK Engineering College</li>
                            <li>Learnt To Apply Theoretical knowledge In Practical Applications</li>
                        </p>
                    </div>
                    <div className="Secondary">
                        <h1>Secondary Education</h1>
                        <p><li>2015 - 2017</li>
                            <li>MPC</li>
                            <li> Sri Chaitanya College</li>
                            <li>83%</li>
                        </p>
                    </div>
                </div>
                <div className="Certificates">
                    <h1 className="edu">Skills</h1>
                    <div className="skille">
                        <p>
                            {skills.map(item => {return( <li key={uuidv1()}><FaQuoteLeft className="i" />{item}</li>) })}
                        </p>
                    </div>
                    <div className="half">
                    <div className="first">
                        <h1 className="edu"> Certifications</h1>
                        
                            <p>
                            {certifications.map(item =><li key={uuidv1()}>{ item}</li>)}
                            </p>
                        
                    </div>
                    <div className="second">
                        <h1 className="edu">PROJECTS</h1>
                             <p>{projects.map(item =><li key={uuidv1()}>{ item}</li>)}</p>
                        </div>
                    </div>    
                </div>
            </div> 
        </div>
    )
}

export default Teja
