import React,{useState} from 'react'
import emailjs from "emailjs-com"
import "./Email.css"
function Email() {
    const [sent, setsent] = useState(false)
    function sentmail(e) {
        e.preventDefault();
        emailjs.sendForm(
					'service_aq0jh1m',
					'template_lbbf9hz',
					e.target,
					'user_wQWDh35Eg9R7vtWtHBqcy',
        )
        setsent(sent => !sent);
        
    }
    return (
        <div style={{ background: "black",paddingTop:"10vh"}}>
				<h1 className="contact-email">Contact Form</h1>
				<form onSubmit={sentmail}>
					<label>Name</label>
					<input type='text' name='name'></input>

					<label>Email</label>
					<input type='email' name='Email'></input>

					<label>Message</label>
                    <textarea rows="4" name='message'></textarea>
                    {sent?<p>Thanks for Contacting</p>:""}

                    <input type="submit" value="Submit"></input>
				</form>
			</div>
		)
}

export default Email
