import React from 'react'
import "./Resume.css"
import { FaCloudDownloadAlt } from "react-icons/fa"
import resume from "./Resume.pdf"

function Resume() {
    return (
        <div>
            <a className="download" href={resume} download><h1><FaCloudDownloadAlt className="ic"/>Download Resume</h1></a>
            <iframe src="https://drive.google.com/file/d/1ovhln4VISKAlx2K1BLFCgoOamvbWfeMq/preview" width="640" height="480" allow="autoplay"></iframe>
        </div>
    )
}

export default Resume
