import React,{useState,useEffect} from 'react'
import "./Navbar.css"
import {NavLink,Link } from "react-router-dom"
import { FaPhoneAlt, FaMailBulk } from 'react-icons/fa'
import { GiHamburgerMenu } from 'react-icons/gi'




function Navbar() {
	const [width, setWidth] = useState(window.innerWidth)
	const [menu, setmenu] = useState(false)
	const [classe, setclasse] = useState('main-menu')
	const updateDimensions = () => {
		setWidth(window.innerWidth)
	}
	useEffect(() => {
		window.addEventListener('resize', updateDimensions)
		return () =>
			window.removeEventListener('resize', updateDimensions)
	}, [])
	
	function clickhandler() {
		setmenu(prev => !prev);
		setclasse(prevclasse => { return prevclasse === "mobile" ? "main-menu" : "mobile" })
		{menu && console.log(menu)}
	};
	return (
		<div className='full'>
				<GiHamburgerMenu className='ham' onClick={clickhandler} />
			<div className='navee'>
				<h1 className='logoo'>
					<Link to='/'>
						<h1 className='Main-logo'>Teja</h1>
					</Link>
				</h1>
				<div className='cta'>
					<p style={{ padding: '1vh 0px' }}>Reach Me At</p>
					<div className='contaact'>
						<a href='tel:+91-9581111727'>
							<FaPhoneAlt />
						</a>
						<a href='mailto:tejavenkatsai8@gmail.com'>
							<FaMailBulk />
						</a>
					</div>
				</div>
				<div
					className={classe}
					onClick={width < 427 ? () => clickhandler() : null}
				>
					<NavLink
						className={(navData) =>
							navData.isActive ? 'active' : ''
						}
						to='/'
					>
						<h4>Home</h4>
					</NavLink>
					<NavLink
						className={(navData) =>
							navData.isActive ? 'active' : ''
						}
						to='/resume'
					>
						<h4>Resume</h4>
					</NavLink>
					<NavLink
						className={(navData) =>
							navData.isActive ? 'active' : ''
						}
						to='/contact'
					>
						<h4>Contact</h4>
					</NavLink>
				</div>
			</div>
		</div>
	)
}

export default Navbar
